import React, { Component } from "react";
import FlipPage from "react-flip-page";
import Header from "./components/header";
import upArrow from "./images/point_arrow_up.png";
import downArrow from "./images/point_arrow_down.png";
import DonatePage from "./pages/donatePage";
import "./App.css";
import BuildingPlans from "./pages/buildingPlans";
import BuildingPlans2 from "./pages/buildingPlans2";
import BuildingPlans3 from "./pages/buildingPlans3";
import Members1 from "./pages/members1";
import Members2 from "./pages/members2";
import MissionStatement from "./pages/missionStatement";
import Members from "./pages/members";

const ANIMATION_DURATION = 400;
const DELAY = 100;
const SCROLLFLIP = 500;
function timeout(delay) {
  return new Promise((res) => setTimeout(res, delay));
}

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pageIndex: 0,
      scrollIndex: 0,
      width: 0,
      height: 0,
      pageFlipping: false,
      greatestHeight:0,
    };
    this.updatePageState = this.updatePageState.bind(this);
    this.flipToPage = this.flipToPage.bind(this);
    this.upPage = this.upPage.bind(this);
    this.downPage = this.downPage.bind(this);
    this.detectScroll = this.detectScroll.bind(this);
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    this.pageFlipStarted = this.pageFlipStarted.bind(this);
    
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener("resize", this.updateWindowDimensions);
   

  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWindowDimensions);

  }
  updateWindowDimensions() {

  
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  detectScroll(e) {
    e.preventDefault();
    let deltaY = e.deltaY;
    if (deltaY < -1) {
      this.setState({
        scrollIndex: this.state.scrollIndex + deltaY,
      });
    }
    if (deltaY > 1) {
      this.setState({
        scrollIndex: this.state.scrollIndex + deltaY,
      });
    }

    if (this.state.scrollIndex >= SCROLLFLIP) {
      this.upPage();
      this.setState({
        scrollIndex: 0,
      });
    }
    if (this.state.scrollIndex <= -SCROLLFLIP) {
      this.downPage();
      this.setState({
        scrollIndex: 0,
      });
    }
  }

  pageFlipStarted(e) {
    this.setState({
      pageFlipping: true,
    });
  }

  updatePageState(e) {
    this.setState({
      pageIndex: e,
      pageFlipping: false,
    });
  }
  upPage() {
    this.flipPage.gotoNextPage();
  }
  downPage() {
    this.flipPage.gotoPreviousPage();
  }
  async flipToPage(index) {
    let flips = index - this.state.pageIndex;
    //flip backwards, lower
    if (flips > 0) {
      while (flips !== 0) {
        this.flipPage.gotoNextPage();
        flips = flips - 1;
        await timeout(ANIMATION_DURATION + DELAY);
      }
    }
    //flip up fowards, higher
    if (flips < 0) {
      while (flips !== 0) {
        this.flipPage.gotoPreviousPage();
        flips = flips + 1;
        await timeout(ANIMATION_DURATION + DELAY);
      }
    }
  }
  render() {

    return (
      <div className="App" onWheel={(e) => this.detectScroll(e)}>
   
        <meta name="viewport" content="width=device-width, initial-scale=1, minimum-scale=1, maximum-scale=1"></meta>
       
        <body style={{height: this.state.height}}>
        <Header FlipToPage={this.flipToPage} ScreenWidth={this.state.width} />
        {this.state.width>800 &&
        <div className="flipPage" style={{height: (this.state.height-50)+'px'}}>
        <FlipPage
            responsive={true}
            uncutPages={false}
            animationDuration={ANIMATION_DURATION}
            showTouchHint={true}
            showHint={true}
            showSwipeHint={true}
            loopForever={true}
            style={{  zIndex: 0 }}
          
            onPageChange={(e) => {
              this.updatePageState(e);
            }}
            ref={(component) => {
              this.flipPage = component;
            }}
          >
         
            <div className="topPage" style={{height: (this.state.height-50)+'px'}}>
                <div alt="image3d" className="drawing3d" style={{height: (this.state.height-50)+'px'}}>
                  <div className="homeTextParent">
                    <h1 className="homeHeading">
                      Here is a super awesome statement to get people to give
                      money ya.
                    </h1>
                    <p className="homeP">
                      here is some supporting text to go along with the heading
                    </p>
                  </div>
                  <button className="donateButton" onClick= {() => {this.flipToPage(1)}}>DONATE</button>
                </div>
              
            </div>
               <DonatePage Mobile={false} Index={this.state.pageIndex}/>
               <BuildingPlans style={{height: (this.state.height-50)+'px'}} Mobile={false} />
            <Members style={{height: (this.state.height-50)+'px'}} Mobile={false}/>
          </FlipPage>
        </div>
        }
        {this.state.width<=800 &&
          <div className="flipPage" style={{height: (this.state.height-50)+'px'}}>
        <FlipPage
            responsive={true}
            uncutPages={false}
            animationDuration={ANIMATION_DURATION}
            showTouchHint={true}
            showHint={true}
            showSwipeHint={true}
            loopForever={true}
            style={{  zIndex: 0 }}
          
            onPageChange={(e) => {
              this.updatePageState(e);
            }}
            ref={(component) => {
              this.flipPage = component;
            }}
          >
         
            <div className="topPage" style={{height: (this.state.height-50)+'px'}}>
                <div alt="image3d" className="drawing3dMobile" style={{height: (this.state.height-50)+'px'}}>
                  <div className="homeTextParentMobile">
                    <h1 className="homeHeading">
                      Here is a super awesome statement to get people to give
                      money.
                    </h1>
                    <p className="homeP">
                     Contact: ahlbergac@gmail.com
                    </p>
                  </div>
                  <button className="donateButton" onClick= {() => {this.flipToPage(1)}}>DONATE</button>
                </div>
            </div>
              <DonatePage width={this.state.width} height={this.state.height} Mobile={true} Index={this.state.pageIndex}/> 
               <BuildingPlans  width={this.state.width} height={this.state.height} Mobile={true} /> 
                <BuildingPlans2 width={this.state.width} height={this.state.height} Mobile={true} />
                <BuildingPlans3 width={this.state.width} height={this.state.height} Mobile={true} />
            <Members1  width={this.state.width} height={this.state.height} Mobile={true}/>
            <Members2  width={this.state.width} height={this.state.height} Mobile={true}/>
          </FlipPage>
        </div>
        }
         
          <div className="arrowContainer">
            <input
              alt="upArrow"
              type="image"
              src={upArrow}
              className="arrow"
              onClick={() => this.downPage()}
            />
            <text className="pageNumber"> {this.state.pageIndex + 1}</text>
            <input
              alt="upArrow"
              type="image"
              src={downArrow}
              className="arrow"
              onClick={() => this.upPage()}
            />
          </div>
       
        </body>
      </div>
    );
  }
}

export default App;
