import React from "react";
import "./buildingPlans3.css";

class buildingPlans3 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className="buildingPlans3Parent" style={{height:(this.props.height - 50)+'px', position:'relative', bottom:'0px'}}>
        <div
          className="floorPlanParent3"
          style={{ fontSize: this.props.height / 38 }}
        > <div className="exteriorParent2">
              <h1  style={{marginTop:'30px'}}>Exterior Elevations</h1>
            </div>
          <p className="buildingPMobile">
          The exterior of the building is designed to blend with City Hall
                in both the use of shed roofs and similar cladding materials.
                Windows will predominately face City Hall and the courtyard and
                include built-in sunshades to aid in sun control. Doors will be
                provided to allow ample access to exterior areas intended to
                host community activities.
          </p>

          <div className="downloadLinkContainer3">
                <div>
                  <button className="downloadLink">Design Plans </button>
                  <i>hereisthenameofthedocument.pdf</i>
                </div>
                <div>
                  <button className="downloadLink">
                    Construction Costs
                  </button>{" "}
                  <i>hereisthenameofthedocument.pdf</i>
                </div>
              </div>
          
        </div>
      </div>
    );
  }
}
export default buildingPlans3;
