import React from "react";
import "./buildingPlans2.css";

class buildingPlans2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className="buildingPlans2Parent" style={{height:(this.props.height - 50)+'px', position:'relative', bottom:'0px'}}>
        <div
          className="floorPlanParent2"
          style={{ fontSize: this.props.height / 38 }}
        >
          <p className="buildingPMobile">
            The Teen Area will house a connection bar for use of electronic
            devices as well as booth seating for study and team projects. A nook
            on the south side is set aside as a Reading Area which may include a
            fireplace if budget allows. A focal Check-Out (Circulation) Desk
            provides ease of access for patrons as well as full visibility for
            staff. The Community Center (east wing) has been designed at roughly
            2,000 square feet and features a nice sized gathering space facing
            south for presentations and performances. A double-sided fireplace
            is shown facing both the gathering space and an exterior patio to
            the east which is again dependent on available funding. A kitchen
            has been included to support Meals on Wheels and other community
            functions. The floor plan is rounded out with an office and support
            spaces.
          </p>
         
        </div>
      </div>
    );
  }
}
export default buildingPlans2;
