import React from "react";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";
import design1 from "../images/designplan1.jpg";
import design2 from "../images/design2.jpg";
import design3 from "../images/design3.jpg";
import design4 from "../images/design4.jpg";

import "./buildingPlans.css";

class buildingPlans extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
    
      <div className="parentContainerBuilding" style={{height:(this.props.height - 50)+'px', position:'relative', bottom:'0px'}}>
        <div className="carouselParent">
          <Carousel
            className="carousel"
            showThumbs={false}
            dynamicHeight={false}
            autoPlay={true}
            emulateTouch={true}
            swipeable={true}
            stopOnHover={true}
            interval={3000}
            infiniteLoop={true}
            showStatus={true}
            showIndicators={true}
            useKeyboardArrows={true}
            transitionTime={200}
            swipeScrollTolerance={5}
          >
            <div className="picDiv">
              <img className="image" src={design1} alt="" />
            </div>
            <div className="picDiv">
              <img className="image" src={design2} alt="" />
            </div>
            <div className="picDiv">
              <img className="image" src={design3} alt="" />
            </div>
            <div className="picDiv">
              <img className="image" src={design4} alt="" />
            </div>
          </Carousel>
        </div>

        {!this.props.Mobile && (
          <div className="designInfoParent">
            <div className=" floorPlanParent">
              <h1 className="buildingHeader"> Floor Plan</h1>
              <p className="buildingP">
                The new Union Gap Library/Community Center will be accessed via
                an entry courtyard designed to welcome visitors as well as to
                host gatherings and casual public use. The lobby serves both the
                Library and Community Center, providing restrooms and support
                facilities for both sides. The Library (west wing) is just over
                2,000 square feet and will feature a generous Children’s Area
                with a reading corner, fun furnishings and mobile shelving to
                allow the flexibility to support a wide range of programs. The
                Teen Area will house a connection bar for use of electronic
                devices as well as booth seating for study and team projects. A
                nook on the south side is set aside as a Reading Area which may
                include a fireplace if budget allows. A focal Check-Out
                (Circulation) Desk provides ease of access for patrons as well
                as full visibility for staff. The Community Center (east wing)
                has been designed at roughly 2,000 square feet and features a
                nice sized gathering space facing south for presentations and
                performances. A double-sided fireplace is shown facing both the
                gathering space and an exterior patio to the east which is again
                dependent on available funding. A kitchen has been included to
                support Meals on Wheels and other community functions. The floor
                plan is rounded out with an office and support spaces.
              </p>
            </div>
            <div className="exteriorParent">
              <h1 className="buildingHeader">Exterior Elevations</h1>
              <p className="buildingP" style={{ fontSize: "min(1.5vw, 16px)" }}>
                The exterior of the building is designed to blend with City Hall
                in both the use of shed roofs and similar cladding materials.
                Windows will predominately face City Hall and the courtyard and
                include built-in sunshades to aid in sun control. Doors will be
                provided to allow ample access to exterior areas intended to
                host community activities.
              </p>
              <div className="downloadLinkContainer">
                <div>
                  <button className="downloadLink">Design Plans </button>
                  <i>hereisthenameofthedocument.pdf</i>
                </div>
                <div>
                  <button className="downloadLink">
                    Construction Costs
                  </button>{" "}
                  <i>hereisthenameofthedocument.pdf</i>
                </div>
              </div>
            </div>
          </div>
        )}
        {this.props.Mobile && (
          <div> 
               <div className="floorPlanParentMobile" style={{fontSize: this.props.height/38}}>
                <h1 className="buildingHeader"> Floor Plan</h1>
                <p className="buildingPMobile">
                The new Union Gap Library/Community Center will be accessed via
                an entry courtyard designed to welcome visitors as well as to
                host gatherings and casual public use. The lobby serves both the
                Library and Community Center, providing restrooms and support
                facilities for both sides. The Library (west wing) is just over
                2,000 square feet and will feature a generous Children’s Area
                with a reading corner, fun furnishings and mobile shelving to
                allow the flexibility to support a wide range of programs. 
                </p>
                </div>
          </div>
        )}
      </div>
  
    );
  }
}
export default buildingPlans;
